.section--main {
  display: flex;
  flex-direction: column;
}

.section--main {
  gap: 2.625rem 0;
}

#submit-button, .button--main {
  border-radius: 1.75rem;
  display: flex;
  justify-content: center;
  padding: 1.125rem 0;
  width: 17.5rem;
}

.button--main {
  background-color: #339ecc;
  color: white;
  font-weight: 700;
}
.button--main:hover {
  background-color: #65e2d9;
  transition: background-color 0.3s ease;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

body {
  background-color: #1B212F;
  margin: 0 auto;
  max-width: 1600px;
  padding: 4.625rem 0 0;
}

h1,
h2,
h3,
h4,
p {
  font-family: "Raleway", sans-serif;
  margin: 0;
}

h1,
h2,
h3 {
  color: white;
}

h1 {
  font-size: clamp(1.4375rem, 5vw, 2.5rem);
}

h2 {
  font-size: clamp(1.125rem, 5vw, 2.5rem);
}

h3 {
  font-size: clamp(1.125rem, 5vw, 2rem);
}

p {
  font-family: "Open Sans", sans-serif;
}

a {
  text-decoration: none;
}

header {
  padding: 0 5rem;
}

footer {
  align-items: flex-start;
  background-color: #0b1523;
  gap: 3.375rem 0;
  height: 27.625rem;
  padding: 10.625rem 5rem 0 7.375rem;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.flex, .flex--social, .flex--footer-block, .flex--footer, .flex--form, .flex--column {
  display: flex;
}
.flex--column {
  flex-direction: column;
}
.flex--j-between {
  justify-content: space-between;
}
.flex--a-center {
  align-items: center;
}
.flex--productive {
  gap: 1.125rem 0;
  padding: 7.25rem 0 0 0;
}
.flex--form {
  gap: 0 1.75rem;
  position: relative;
  width: 100%;
}
.flex--footer {
  align-items: flex-start;
  justify-content: space-between;
}
.flex--footer-block {
  align-items: flex-start;
  gap: 0 1.75rem;
  flex: 0 1 23.25rem;
}
.flex--social {
  gap: 0 0.625rem;
}
.flex--social i {
  color: white;
  font-size: 1.5rem;
  transition: color 0.3s ease, transform 0.3s ease;
}
.flex--social i:hover {
  color: #65e2d9;
  transform: scale(1.1);
}

.grid, .grid--feature-block, .grid--feature {
  display: grid;
}
.grid--feature {
  background-color: #181f2b;
  gap: 0 9.75rem;
  grid-template-columns: repeat(2, minmax(15rem, 22.625rem));
  grid-template-rows: 19rem auto;
  justify-content: center;
  padding: 0 5rem 8.625rem;
}
.grid--feature-block {
  grid-template-columns: 1fr;
  grid-template-rows: 7rem 2.0625rem auto;
  justify-items: center;
  text-align: center;
}

.navbar {
  gap: 0 3.625rem;
}
.navbar a {
  color: #c0c0c0;
  position: relative;
  transition: text-shadow 0.3s ease;
}
.navbar a:hover {
  text-shadow: 1px 0 0px white;
}
.navbar a:hover::before {
  width: 100%;
}
.navbar a::before {
  content: "";
  background-color: white;
  bottom: -10%;
  height: 1px;
  left: 0;
  position: absolute;
  transition: width 0.3s ease;
  width: 0;
}

.hero {
  background: #1B212F url("../images/bg-curvy-desktop.svg") no-repeat center bottom;
  background-size: 100% auto;
  padding: 4.375rem 5rem 10.75rem;
}
.hero > img {
  margin-bottom: 2.625rem;
}
.hero p {
  line-height: 1.875rem;
  width: 38rem;
}

.productive {
  align-items: flex-start;
  background-color: #181f2b;
  gap: 0 3.625rem;
  padding: 0 6rem 11.125rem 6.5625rem;
}
.productive > img {
  height: 29.0625rem;
  width: clamp(20rem, 40vw, 38.4375rem);
}
.productive h2 {
  margin-bottom: 0.25rem;
}

.testimonial {
  background-color: #181f2b;
  gap: 2.5rem;
  grid-template-columns: repeat(3, minmax(15rem, 22.25rem));
  grid-template-rows: auto;
  justify-content: center;
  padding: 0 5rem 5.25rem;
  position: relative;
}
.testimonial--block {
  background-color: #202a3c;
  border-radius: 0.25rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem 0;
  padding: 2.5rem 1.25rem;
  position: relative;
}
.testimonial::before {
  content: url(../images/bg-quotes.png);
  left: 9.8%;
  position: absolute;
  top: -2.25rem;
}

.position {
  gap: 0 0.375rem;
}
.position--image {
  border-radius: 50%;
  height: 1.625rem;
  width: 1.625rem;
}
.position--staff {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.cta {
  background-color: #181f2b;
  text-align: center;
}
.cta--holder {
  background-color: #1c2431;
  border-radius: 0.25rem;
  box-shadow: 0.5rem 0.5rem 1rem rgba(0, 0, 0, 0.3);
  gap: 1.4375rem 0;
  height: 17.1875rem;
  margin: 0 auto;
  padding: 3rem 4.625rem 0;
  transform: translateY(7.25rem);
  width: 53.875rem;
}

#email {
  border-radius: 2rem;
  font-family: "Open Sans", sans-serif;
  height: 3rem;
  padding-left: 2.25rem;
  width: 30rem;
}

#submit-button {
  align-items: center;
  background-image: linear-gradient(126deg, #65e2d9, #339ecc);
  color: white;
  cursor: pointer;
  font-family: "Open Sans", sans-serif;
  font-size: 0.875rem;
  font-weight: 700;
  height: 3rem;
  opacity: 0.8;
  transition: opacity 0.3s ease;
  width: 12.5rem;
}
#submit-button:hover {
  opacity: 1;
}

.raleway {
  font-family: "Raleway", sans-serif;
}

.open-sans {
  font-family: "Open Sans", sans-serif;
}

.section--main {
  align-items: center;
}

.section--hero {
  margin-bottom: 2.0625rem;
  text-align: center;
  width: 46.25rem;
}

.f-20 {
  color: #eeeeee;
  font-size: 1.25rem;
}

.f-16 {
  color: #c0c0c0;
  line-height: 1.5rem;
  font-size: 1rem;
}

.f-15 {
  color: #c0c0c0;
  font-size: 0.9375rem;
}

.f-14 {
  color: #c0c0c0;
  font-size: 0.875rem;
}

.f-11 {
  color: white;
  font-size: 0.6875rem;
  font-weight: 700;
}

.f-8 {
  color: #c0c0c0;
  font-size: 0.5rem;
}

.fylo-link {
  align-self: flex-start;
  color: #65e2d9;
  position: relative;
  transition: color 1s ease;
}
.fylo-link::after {
  content: "";
  background-color: #65e2d9;
  bottom: -10%;
  height: 1px;
  left: 0;
  position: absolute;
  transition: background-color 1s ease;
  width: 100%;
}
.fylo-link svg {
  transform: translate(0.25rem, 10%);
}
.fylo-link circle {
  transition: fill 1s ease;
}
.fylo-link:hover {
  color: white;
}
.fylo-link:hover::after {
  background-color: white;
}
.fylo-link:hover circle {
  fill: white;
}
.fylo-link:hover svg {
  animation: go-right 1.5s ease infinite;
}

.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.footer--info {
  gap: 1.5rem 0;
}
.footer--info .flex--footer-block {
  flex: 0 1 auto;
}

.footer--links {
  font-family: "Open Sans", sans-serif;
  gap: 1rem 0;
}

.footer-link {
  transition: 0.5s ease;
}
.footer-link:hover {
  color: white;
}

.error-form,
.success-form {
  bottom: -40%;
  font-size: 0.625rem;
  font-weight: 700;
  left: 2rem;
  position: absolute;
  visibility: hidden;
}

.show-form {
  visibility: visible;
}

.error-form {
  color: #ff4242;
}

.success-form {
  color: #65e2d9;
}

.translate-right {
  transform: translateX(2rem);
}

.translate-left {
  transform: translateX(-2rem);
}

.translate-up {
  transform: translateY(2rem);
}

.translate-right,
.translate-left,
.translate-up {
  opacity: 0;
  transition: opacity 1s ease, transform 1s ease;
}

.show-transform {
  opacity: 1;
  transform: translateX(0) translateY(0);
}

@keyframes go-right {
  50% {
    transform: translateX(1rem);
  }
}
@media (max-width: 62.5em) {
  body {
    padding: 4.625rem 0 0;
  }

  footer {
    height: auto;
  }

  .flex--productive {
    align-items: center;
  }
  .flex--footer {
    flex-wrap: wrap;
    gap: 2rem;
  }
  .flex--form {
    align-items: center;
    flex-direction: column;
    gap: 1rem 0;
  }

  .productive {
    align-items: center;
    flex-direction: column;
    text-align: center;
  }
  .productive > img {
    height: auto;
    width: 80%;
  }
  .productive br {
    display: none;
  }

  .testimonial {
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  }

  .cta {
    width: 100%;
  }
  .cta--holder {
    height: auto;
    padding: 3rem 4.625rem 1rem;
    width: 90%;
  }

  .fylo-link {
    align-self: center;
  }

  .error-form,
.success-form {
    bottom: auto;
    left: 0;
    text-align: center;
    top: 0;
    transform: translatey(-100%);
    width: 100%;
  }
}
@media (max-width: 53.75em) {
  header {
    padding: 0 2rem;
  }

  footer {
    padding: 10.625rem 2rem 2rem;
  }

  .grid--feature {
    gap: 1.5rem 1rem;
    grid-template-rows: auto;
    padding: 0 2rem 8.625rem;
  }

  .hero {
    padding: 4.375rem 2rem 10.75rem;
  }
  .hero > img {
    width: 100%;
  }

  .productive {
    padding: 0 2rem 11.125rem 2rem;
  }

  .testimonial {
    padding: 0 2rem 5.25rem;
  }

  .section--hero {
    width: 100%;
  }
}
@media (max-width: 39.125em) {
  body {
    padding-top: 1.5rem;
  }

  header img {
    width: 5rem;
  }

  .navbar {
    font-size: 0.75rem;
    gap: 0 2rem;
  }

  .flex--productive {
    align-items: flex-start;
  }
  .flex--footer {
    flex-direction: column;
  }
  .flex--footer-block {
    flex: 0;
  }
  .flex--social {
    align-self: center;
    gap: 0 1.25rem;
  }

  .grid--feature {
    gap: 5.5rem 0;
    grid-template-columns: 1fr;
  }

  .hero {
    background-image: url("../images/bg-curvy-mobile.svg");
  }
  .hero p {
    font-size: 0.875rem;
    width: 100%;
  }

  .productive {
    text-align: left;
  }

  .testimonial {
    grid-template-columns: 1fr;
  }

  .cta--holder {
    padding: 3rem 2rem 1rem;
  }

  .fylo-link {
    align-self: flex-start;
  }

  #email {
    width: 100%;
  }

  .footer--info {
    margin-bottom: 2rem;
  }
}